let importAll = (requireContext:__WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext);
try {
  importAll(require.context('../assets/icons', true, /\.svg$/));
}catch(error){
  console.log(error);
}
//将svg统一全部引入

export default {
  props:['name'],
  name: 'Icon'
};
